<template>
	<div class="sum_wrap">
		<div class="search">			
			<div class="pay">
				<div class="pay_way">
					<span>支付方式</span>
					<select name="" id="" ref="way"  @change="change($event)">
						<option value="全部" selected>全部</option>
						<option value="微信扫码">微信扫码</option>
						<option value="支付宝扫码">支付宝扫码</option>
						<option value="微信H5">微信H5</option>
						<option value="微信公众号">微信公众号</option>
						<option value="微信sdk支付">微信sdk支付</option>
					</select>
				</div>
				<div class="pay_state">
					<span>支付状态</span>
					<select name="" id="" ref="status" @change="change($event)">
						<option value="全部" selected>全部</option>
						<option value="2">支付成功</option>
						<option value="3">支付失败</option>
						<option value="4">支付处理中</option>
					</select>
				</div>
			</div>
			<div class="time">
				<span>统计时间</span>
				<input type="text" id="date1" class="laydate-icon" placeholder="-日期选择-"  ref="start" @click="change( $event)"/>
				<span>至</span>
				<input type="text" id="date2" class="laydate-icon" placeholder="-日期选择-"  ref="end" @click="change( $event)"/>
				<button @click="today()">今天</button>
				<button @click="yesterday()">昨天</button>
			</div>
			<div class="goods">
				<div class="bank_num">
					<span>银行商户号</span>
					<input type="text" placeholder="请输入银行商户号"  ref="order" @change="change($event)"/>
				</div>
				<div class="order">
					
					<span>订单号</span>
					<input type="text" placeholder="请输入订单号"  ref="order" @change="change($event)"/>
					<button>查询</button>
				</div>
			</div>
		</div>
		<div class="totle">
			<p>总交易笔数:<span>{{count}}</span>笔</p>
			<p>总交易额:<span>{{sum}}</span>元</p>
			<p><img src="../../assets/img/ht_excel.png" alt="" /><a><span>导出Excel</span></a></p>
		</div>
		<table class="table table-striped">
  			<thead>
  				<tr>
  					<td>接入方流水号</td>
  					<td>银行商户号</td>
  					<td>接入方提交日期</td>
  					<td>接入方提交时间</td>
  					<td>平台流水号</td>
  					<td>平台交易日期</td>
  					<td>平台交易时间</td>
  					<td>账号</td>
  					<td>支付类型</td>
  					<td>交易类型</td>
  					<td>交易状态</td>
  					<td>交易金额</td>
  					<td>手续费</td>
  					<td>结算金额</td>
  				</tr>
  			</thead>
  				<tbody>
  					<tr v-for="(i,index) in orderList" :key="index" >
  						<td >{{i.order}}</td>
  						<td>{{i.bank_merchant_number}}</td>
  						<td>{{i.access_submit_date.substring(0,10)}}</td>
  						<td>{{i.access_submit_date.substring(11)}}</td>
  						<td>{{i.platform_seq}}</td>
  						<td>{{i.platform_trade_date.substring(0,10)}}</td>
  						<td>{{i.platform_trade_date.substring(11)}}</td>
  						<td>{{i.account}}</td>
  						<td>{{i.pay_type=='wechat'?'微信':'QQ'}}</td>
  						<td>{{i.trade_type}}</td>
  						<td>{{i.pay_status}}</td>
  						<td class="color">{{i.trade_amount}}</td>
  						<td>{{i.poundage}}</td>
  						<td>{{i.settling_amount}}</td>
  					</tr>
  				</tbody> 			
		</table>
		
		<div class="page_wrap">
		  	<div class="left">
		  		<span>每页显示</span>
			  	<select name="">
			  		<option value="10">10条</option>
			  		<option value="20">20条</option>
			  		<option value="50">50条</option>
			  		<option value="100">100条</option>
			  	</select>
		  	</div>
		    <ul class="pagination" >
		            <li v-show="current != 1" @click="current-- && goto(current)" ><a class="last">上一页</a></li>
		            <li v-for="index in pages" @click="goto(index)" :class="{'active':current == index}" :key="index">
		              <a >{{index}}</a>
		            </li>
		            <li v-show="allpage != current && allpage != 0 " @click="current++ && goto(current++)" ><a class="next">下一页</a></li>
		    </ul>
		    <div class="right">
		    	<span>共46页</span>
		    	<span>共计455笔交易</span>
		    </div>    
		</div>
	</div>
</template>

<script>
	
	import $ from '../../../static/libs/jquery-1.10.1.min.js';
	import '../../../static/laydate/laydate.js';
	export default{
		data(){
			return{
				orderList:null,
				count:null,
				sum:null,
				ways:'',
				current:1,
	            showItem:5,
	            allpage:13,
				
				
			}
		},
		created(){
			this.ajax(4)
//       console.log(this.childList)
//			this.$nextTick(()=>{
//				laydate({
//				elem:'#date1',
//				//event:'click'
//			}),
////			laydate({
////				elem:'#date2',
////				event:'click'
////			})
//			})
			
		},
		beforeMount(){
			this.getData()
			
		},
		mounted(){			
//			console.log(this.orderList)
//			console.log(this.$refs.way.value)
//			console.log(this.$refs.status.value)
//			console.log(this.$refs.start.value)
//			console.log(this.$refs.end.value)
//			console.log(this.$refs.order.value)
//			console.log(typeof(this.orderList))
			
		},
		methods:{
			getData(page,num,type,status,start,end,order,bank){
				var that=this
				$.ajax({
					url:"http://192.168.200.127:5000/bill/query-bill?phone=18607150257",
//					url:"../../../static/pay.json",
					
					type:"get",				
					success:function(data){
//						console.log(data)
//						console.log(JSON.parse(data))
//						console.log(that.orderList)
//						console.log(JSON.parse(data).bills)
						that.orderList=JSON.parse(data).bills
//						that.orderList=data.bills
//						console.log(that.orderList)
						that.sum=JSON.parse(data).sum
						that.count=JSON.parse(data).counts
					}
				})	
			},
			today(){
				var day2 = new Date();
				day2.setTime(day2.getTime());
				var month2=day2.getMonth()+1
				var s2 = day2.getFullYear()+"-" + (month2<=9?'0'+month2:month2) + "-" + day2.getDate();
				console.log(s2)
			},
			yesterday(){
				var day1 = new Date();
				 day1.setTime(day1.getTime()-24*60*60*1000);
				 var month1=day1.getMonth()+1
				 var s1 = day1.getFullYear()+"-" + (month1<=9?'0'+month1:month1) + "-" + day1.getDate();
				 console.log(s1)
			},
			change(event){
				console.log(event.target.value)
				let that = this
				let target = event.target.id				
					laydate({
						elem:'#'+target+'',
					//event:'click'
					choose: function(datas){ //选择日期完毕的回调
//					        console.log(that.$refs.start.value)
//							console.log(that.$refs.end.value)
							console.log(datas)
//							console.log(laydate.now(-1))
//							console.log(laydate.now(0))
					    }
					})
			},
			goto(index){
	          if(index == this.current) return;
	            this.current = index;
	            //这里可以发送ajax请求
	           this.ajax(13)
	        
	        },
	        ajax(page,num,type,status,start,end,order,bank) {
	             $.ajax({	             	url:"http://192.168.200.127:5000/bill/query-bill?phone=18607150257&number="+num+"&page="+page+"&type="+type+"&status="+status+"&end="+end+"&order="+order+"&bank="+bank+"",
	             	type:"get",
//	               url:"htto://sdasda?page="+page+"&number="+number+"",
	                success:function(data){
	                   console.log(data) 
	                } 
	             })
	            this.allpage = page
	        }
		},
		computed:{
          pages:function(){
                var pag = [];
                  if( this.current < this.showItem ){ //如果当前的激活的项 小于要显示的条数
                       //总页数和要显示的条数那个大就显示多少条
                       var i = Math.min(this.showItem,this.allpage);
                       while(i){
                           pag.unshift(i--);
                       }
                   }else{ //当前页数大于显示页数了
                       var middle = this.current - Math.floor(this.showItem / 2 ),//从哪里开始
                           i = this.showItem;
                       if( middle >  (this.allpage - this.showItem)  ){
                           middle = (this.allpage - this.showItem) + 1
                       }
                       while(i--){
                           pag.push( middle++ );
                       }
                   }
                 return pag
               }
      },

	}
</script>

<style  type="text/css" scoped="scoped">
	table{
		box-sizing: border-box;
	}
	tr{
		box-sizing: border-box;
	}
	tr:nth-of-type(odd) {
    background-color: #f9f9f9;
	}
	td{
		padding: 8px;
		text-align: center;
		box-sizing: border-box;
		border-top: 1px solid #ddd;
	}
	.pagination {
        position: relative;
      }
      .pagination li{
        display: inline-block;
        margin:0 6px;
      }
      .pagination li a{
      	width: 30px;
      	height: 30px;
      	line-height: 30px;
      	text-align: center;
        display:inline-block;
        border:1px solid #bababa;
        background:#fff;
        color:#666;
      }
      .pagination li a:hover{
        background:#eee;
      }
      .pagination li.active a{
        border:1px solid #42a5df;
        color:#42a5df;
      }
      .last,.next{
      	display: block !important;
      	width: 64px !important;
      	height: 30px !important;
      }
      .left,.right,.pagination{
      	float: left;
      }
      .left,.right{
      	height: 32px;
      	line-height: 30px;
      }
      .left,.right span,option,select{
      		font-size: 14px;
      		color: #666;
      	}
      .page_wrap{
      	width: 800px;
      	height: 32px;
      	margin: 0 auto;
      	margin-top: 56px;
      	
      }
	
</style>


