<template>
	<div class="checkOrder_wrap">
		<header>
			<div class="logo"><router-link to="/"><img src="../../assets/img/ht.png" alt="" /></router-link></div>
			<ul>
				<li v-if="info?true:false" style="color: #fff;">
					{{info.replace(/(\d{3})(\d{4})(\d{4})/,"$1****$3")}} 
				</li>
				<li v-if="info?true:false" style="color: #fff;" @click="exit()">
					[退出]
				</li>
			</ul>
		 </header>
		 <nav>
			<ul>
				<li><router-link to="/checkOrder/sum"><img src="../../assets/img/ht_menu_ic_yytj.png" alt="" /><span>交易统计</span></router-link></li>
				
			</ul>
		</nav>
		<router-view></router-view>
	</div>
</template>

<script>
	import vsum from './sum'
	export default{
		data(){
			return{
				info:window.sessionStorage.username
			}
		},
		methods:{
			exit(){
				window.sessionStorage.username=""
				this.info=""
			}
		},
		components:{
			vsum,
		},
	}
</script>

<style>
</style>


