<template>
	<div class="sec_bg">
	<section>
		<div class="title">
			<p>商品名称 &nbsp;: &nbsp;<span>测试应用-支付功能体验（非商品消费）</span></p>
			<p>订单编号 &nbsp;: &nbsp;<span>32441707071639208331</span></p>
			<p>金额: &nbsp;<span>¥0.01</span></p>
		</div>
		<div class="pay_content" v-show="show">
			<div class="pay_way">
				<h1>请选择支付方式</h1>
				<ul>
					<li v-for="(i,index) in list" :key="index" @click="changeIndex(index)" :class="liIndex==index?'li-active':''">
						<img :src="i.src" alt="" />
						<span>{{i.payway}}</span>
					</li>
				</ul>
			</div>
			<div class="paybox">
				<p>需支付:0.01元</p>
				<router-link to="">
					<div @click="showCon">立即支付</div>
				</router-link>
			</div>
			<div class="pay_word">
				<p><span>*</span>本页面仅用于支付测试及体验，任何利用本页面产生的订单信息于其他用途等行为，我要付不承担任何责任，并保留追究法律责任的权利</p>
				<p><span>*</span>本次体验为真实付款，体验金额不予退还，请勿大额支付</p>
			</div>
			<div class="layout" v-show="layout">
				<p>订单支付超时，请返回商户重新下单</p>
				<p><span>{{mytime}}</span>秒后将自动返回到商户</p>
				<!--<span @click="times()">k</span>-->
			</div>
		</div>
		<div class="wx_pay" v-show="wx">
			<h1><img src="../../assets/img/demo/demo_pc_wechat.png" alt="" />微信支付</h1>
			<h2>通过微信 &nbsp;[扫一扫] &nbsp;扫描二维码进行支付</h2>
			<div class="img_box">
				<!--<img src="../../assets/img/demo/demo_pc_ewm.png" alt="" />-->
				<div id="qrcode">
			        <div id="codeico"></div>
			    </div>
				<img src="../../assets/img/demo/demo_pc_wechat_phone.png" alt="" />
			</div>
			<p @click="showOther()">其他支付方式 &nbsp;></p>
		</div>
		<div class="yl_pay" v-show="yl">
			<h1><img src="../../assets/img/demo/demo_pc_yl.png" alt="" />银联支付</h1>
			<div><i>银行卡号</i><input type="number" placeholder="请输入银行卡号"/></div>
			<div><i>手机号</i><input type="tel" placeholder="请输入手机号"/></div>
			<div><i>验证码</i><input type="text" placeholder="请输入验证码"/><span>点击获取</span></div>
			<button>立即支付</button>
			<p @click="showOther()">其他支付方式 &nbsp;></p>
		</div>
		<div class="ali_pay" v-show="ali">
			<h1><img src="../../assets/img/demo/demo_pc_ali.png" alt="" />支付宝付款</h1>
			<h2>请通过支付宝 &nbsp;[扫一扫] &nbsp;扫描二维码进行支付</h2>
			<div class="img_box">
				<img src="../../assets/img/demo/demo_pc_ewm.png" alt="" />				
				<img src="../../assets/img/demo/demo_pc_ali_phone.png" alt="" />
			</div>
			<p @click="showOther()">其他支付方式 &nbsp;></p>
		</div>
		
		
	</section>
	</div>
</template>

<script>
	import $ from '../../../static/libs/jquery-1.10.1.min.js';
	import '../../../static/libs/code.js';
	export default {
  		data () {
   		 return {
   		 	list:[
   		 		{src:require("../../assets/img/demo/demo_pc_wechat.png"),payway:"微信支付"},
   		 		{src:require("../../assets/img/demo/demo_pc_yl.png"),payway:"银联支付"},
   		 		{src:require("../../assets/img/demo/demo_pc_ali.png"),payway:"支付宝"}
   		 	],
   		 	liIndex:0,
   		 	show:true,
   		 	wx:false,
   		 	yl:false,
   		 	ali:false,
   		 	layout:false,
   		 	mytime:5,   		 	
   		 }
  		},
  		computed:{
  			
  		},
  		created(){
			this.init()
			
  		},
  		methods:{
  			changeIndex(index){
  				console.log(index);
				this.liIndex=index;
				console.log(this.liIndex);
  			},
  			//设置一个方法，判断选中项下标，并将相应的v-show值改变来控制显示隐藏
			showCon(){
  				console.log(this.show)
				this.show=!this.show;
				console.log(this.liIndex)
				if(this.liIndex==0){
					this.wx=true
				}
				if(this.liIndex==1){
					this.yl=true
				}
				if(this.liIndex==2){
					this.ali=true
				}
				console.log(this.wx);
				console.log(this.yl);
				console.log(this.ali);
  			},
  			//点击其他支付方式时，对应的v-show值控制显示隐藏
  			showOther(){
  				this.show=true;
  				this.wx=false;
  				this.yl=false;
  				this.ali=false;
  			},
  			//弹出层倒计时
  			times(){
  				var that=this;
  				var interval=setInterval(function(){  					
  					console.log(that.mytime)
  					if((that.mytime--)<=0){
  						that.layout=false; 						
  						clearInterval(interval);
  					} 					
  				},1000)
  			},
		  	generateQRCode(rendermethod, picwidth, picheight, url) {
		  		
		  		let that = this
		  		let text = that.utf16to8(url)
		  		this.$nextTick(()=>{
		  			$("#qrcode").qrcode({ 
			            render: rendermethod, // 渲染方式有table方式（IE兼容）和canvas方式
			            width: picwidth, //宽度 
			            height:picheight, //高度 
			            text: text, //内容 
			            typeNumber:-1,//计算模式
			            correctLevel:2,//二维码纠错级别
			            background:"#ffffff",//背景颜色
			            foreground:"#000000"  //二维码颜色
		        	})
		  		})
		        
		       
		   },
		   init() {
		        this.generateQRCode("table",200, 200, "http://wwww.baidu.com");		        
		   },
		   utf16to8(str) {
		        var out, i, len, c;
		        out = "";
		        len = str.length;
		        for (i = 0; i < len; i++) {
		            c = str.charCodeAt(i);
		            if ((c >= 0x0001) && (c <= 0x007F)) {
		                out += str.charAt(i);
		            } else if (c > 0x07FF) {
		                out += String.fromCharCode(0xE0 | ((c >> 12) & 0x0F));
		                out += String.fromCharCode(0x80 | ((c >> 6) & 0x3F));
		                out += String.fromCharCode(0x80 | ((c >> 0) & 0x3F));
		            } else {
		                out += String.fromCharCode(0xC0 | ((c >> 6) & 0x1F));
		                out += String.fromCharCode(0x80 | ((c >> 0) & 0x3F));
		            }
		        }
		        return out;
		    } 
			
  		}
}
</script>

<style>
</style>


